.offer-aside {
  display: flex;
  flex-direction: column;
  row-gap: 20px;

  & .offer-aside__deal {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
  }

  & .offer-aside__statistics {
    font-size: 14px;

    & p {
      display: flex;
      column-gap: 10px;
      align-items: center;
      color: #737a8e;
    }
  }
}

@media (min-width: @desktop-width) {
  .offer-aside {
    & .seller__wrapper {
      flex-direction: column;
      align-items: center;
      column-gap: 20px;
    }
  }

  & .offer-aside__deal {
    row-gap: 30px;
    padding: 20px;
    box-shadow: 0 0 10px var(--light-gray);
    border-radius: 5px;
  }

  & .offer-aside__statistics {
    padding: 20px;
    box-shadow: 0 0 10px var(--light-gray);
    border-radius: 5px;
  }
}
